var index = r => require.ensure([], () => r(require('./pages/index.vue')), 'index');
var articleParty = r => require.ensure([], () => r(require('./pages/article-party.vue')), 'party-zenekar');
var articleEvent = r => require.ensure([], () => r(require('./pages/article-event.vue')), 'zenekar-rendezvenyre');
var articleWedding = r => require.ensure([], () => r(require('./pages/article-wedding.vue')), 'zenekar-eskuvore');
//var downloads = r => require.ensure([], () => r(require('./pages/downloads.vue')), 'letoltesek');
var gallery = r => require.ensure([], () => r(require('./pages/gallery.vue')), 'galeria');
var opinions = r => require.ensure([], () => r(require('./pages/opinions.vue')), 'video');
var partners = r => require.ensure([], () => r(require('./pages/partners.vue')), 'partnereink');
var repertoire = r => require.ensure([], () => r(require('./pages/repertoire.vue')), 'repertoar');
var video = r => require.ensure([], () => r(require('./pages/video.vue')), 'velemenyek');
var contact = r => require.ensure([], () => r(require('./pages/contact.vue')), 'kapcsolat');

var routes = [
  {
    path: '/',
    component: index,
    data: {
      linkName: 'Foldal',
      metaDesc: 'Ok Zenekar lakodalom party bl eksv dj Zenei knlat kompromisszum nlkl!'
    }
  },
  {
    path: '/zenekar-eskuvore',
    component: articleWedding,
    data: {
      category: 'Szolgltatsok',
      linkName: 'Zenekar eskvre',
      title: 'Profi Zenekar Eskvre - Szles Dalvlasztk, 100% Elgedettsg',
      metaDesc: 'Az OK Zenekar a legjobb vlaszts ha minsg lzent szeretne eskvjre. Profi hangtechnika s fergetes hangulat. Legyen tkletes eskvje velnk!'
    }
  },
  {
    path: '/party-zenekar',
    component: articleParty,
    data: {
      category: 'Szolgltatsok',
      title: 'Party Zenekar Duo - Felhtlen partyhangulat, Prmium minsg',
      linkName: 'Party Zenekar',
      metaDesc: 'Az OK Zenekar kzel 30 ves tapasztalattal rendelkezik s szmos rendezvnyen vettek rszt, mint party zenekar. Szles mfajvlasztk s profi hangtechnika!'
    }
  },
  {
    path: '/rendezveny-zenekar',
    component: articleEvent,
    data: {
      category: 'Szolgltatsok',
      linkName: 'Rendezvny Zenekar',
      title: 'Profi Rendezvny Zenekar - Szles Repertor, 100% lzene ',
      metaDesc: 'Az OK zenekar tkletes vlaszts cges rendezvnyek, blok, szletsnapok, partyk, eskvk zenei hangulatnak biztostsra. zig-vrig lzene!!'
    }
  },
  {
    path: '/galeria',
    component: gallery,
    data: {
      linkName: 'Galria',
      metaDesc: 'Ftk a zenekar tagjairl, bulikrl, rendezvnyekrl.'
    }
  },
  {
    path: '/video',
    component: video,
    data: {
      linkName: 'Vide',
      metaDesc: 'Videink'
    }
  },
  {
    path: '/repertoar',
    component: repertoire,
    data: {
      linkName: 'Repertor',
      metaDesc: 'Repertorunk'
    }
  },
  {
    path: '/velemenyek',
    component: opinions,
    data: {
      linkName: 'Vlemnyek',
      metaDesc: 'Vlemnyek a zenekarrl'
    }
  },
  {
    path: '/kapcsolat',
    component: contact,
    data: {
      linkName: 'Kapcsolat',
      metaDesc: 'lpjen Kapcsolatba velnk'
    }
  },
  /*{
    path: '/letoltesek',
    component: downloads,
    data: {
      skipMenu: true,
      linkName: 'Letltsek',
      metaDesc: 'Zenk letltse'
    }
  },*/
  {
    path: '/partnereink',
    component: partners,
    data: {
      skipMenu: true,
      linkName: 'Partnereink',
      metaDesc: 'Partnereink'
    }
  },
]

//console.log(routes);

module.exports = routes;



